export const reColourMarkers = async (provider) => {
  const { graphicsLayer, targetMarker, isDark } = provider
  if (!targetMarker) return
  const graphic = targetMarker.clone()
  graphic.symbol.color = isDark ? '#ffffff' : '#0b0c0c'
  graphicsLayer.remove(targetMarker)
  graphicsLayer.add(graphic)
  provider.targetMarker = graphic
}
